@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;500;600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:600);
/* Fonts */

/* Reset CSS */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* Universal Styles */

html {
  font-size: 18px;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: 'Work Sans', sans-serif;
}

h1 {
  padding: .66rem 0;
  text-align: center;
  background-color: #be1e2d;
  font-family: Poppins, sans-serif;
  font-size: 1.8rem;
  color: #fff;
}
.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url(/foodplace/static/media/bkg_search_desktop.a0aa6d98.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 30rem;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #fff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem 0.33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: 0.83rem;
  transition: all .5s;
}

.SearchBar-sort-options li:hover {
  opacity: .8;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #be1e2d;
  color: #be1e2d;
}

.SearchBar-fields {
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;
}

.SearchBar-fields input {
  width: 21rem;
  padding: 0.66rem 1rem;
  margin-right: 2.22rem;
  border: 2px solid #fff;
  border-radius: 4px;
  font-size: 0.77rem;
  font-weight: 500;
  opacity: 0.7;
  transition: all .5s;
}

.SearchBar-fields input:focus {
  border: 2px solid #be1e2d;
  opacity: 1;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
}

.SearchBar-submit button {
  border: 0;
  border-radius: 5px;
  outline: none;
  padding: 1rem 3rem;
  background-color: #be1e2d;
  font-size: .9rem;
  color: #fff;
  font-weight: 600;
  transition: all .5s;
}

.SearchBar-submit button:hover {
  cursor: pointer;
  opacity: .8;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/foodplace/static/media/bkg_search_mobile.cf9dbfa2.jpg);
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: 0.86rem;
  }
}
.BusinessList {
  display: flex;
  margin: 5% 10%;
  justify-content: space-around;
  align-items: flex-end;
  flex-wrap: wrap;
}
.Business {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 16.66rem;
  margin: 0 0.5rem 2.3rem 0.5rem;
}

.Business .image-container {
  height: 16.66rem;
  margin-bottom: 1rem;
}

.Business h2 {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Business-information {
  display: flex;
  justify-content: space-between;
}

.Business-information p {
  font-size: 0.88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Business-address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Business-reviews {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}

.Business-reviews h3 {
  color: #be1e2d;
  font-weight: 600;
}

.Business-reviews .rating {
  font-size: 0.88rem;
  line-height: 1rem;
}

